<template>
  <div id="container"></div>
</template>

<script>
import AMapLoader from '@amap/amap-jsapi-loader'
import maps from '../api/map'
window._AMapSecurityConfig = {
  securityJsCode: 'f057abc990551ea29580ba15e87e032b'
}
export default {
    name:'mapContain',
    components:{maps},
    data(){
        return{
           map:null,
           key:'81883048083422e2b1547a6eeaf09475',
           position:[]
        }
        
    },
    props:['address'],
    methods:{
        getplace(){ //
            console.log(this.address)
           maps({
              url:`/v3/geocode/geo?address=${this.address}&output=json&key=${this.key}`,
           }).then(res=>{
               this.position.push(res.data.geocodes[0].location)
            })
        },
        initMap() {
      AMapLoader.load({
        key: '756a7bf4659ad699a7f819fb665f1103', // WebKey load 
        version: '2.0', //  JSAPI  1.4.15
        plugins: [''] // 'AMap.Scale'
      })
        .then(AMap => {
            let that=this
          this.map = new AMap.Map('container', {
            //id
            viewMode: '3D', //3D
            zoom: 13, //
            center: that.aposition //
          })
          
        })
        .catch(e => {
          console.log(e)
        })
    }
    },
    mounted() {
        this.getplace()
    //DOM
    this.initMap()
    }

}
</script>

<style>
  #container{
    height: 300px;
  }
</style>